import React, { useState, useEffect } from 'react'
import "prismjs/themes/prism-tomorrow.css";
import { NotionRenderer } from "react-notion";
import {
    BrowserRouter as Router,
    Routes,
    Route,
    Link,
    useParams
  } from "react-router-dom";

const Post = () => {
  let { id } = useParams()
    const [Posts, setPost] = useState({})
    useEffect(()=> {
        fetch(`https://notion-api.splitbee.io/v1/page/${id}`)
          .then((res) => {
            if(res.ok) {
              console.log(res)
              return res.json()
            } 
            throw res
          })
          .then(post => {
            setPost(post)
          })
          .catch(error => {
            console.error("error tetching data: ", error)
    
          })
      }, [])
    return(
        <div>

          <NotionRenderer 
  blockMap={Posts} />

        </div>
    )
}

export default Post