import React, { useState, useEffect } from 'react'
import {
    BrowserRouter as Router,
    Link,
  } from "react-router-dom";
const NOTION_BLOG_ID = process.env.NOTION_BLOG_ID || "5b53abc87b284beab0c169c9fb695b4d";

const Blog = ({...props}) => {
    const [Posts, setPosts] = useState([])
    useEffect(()=> {
        fetch(`https://notion-api.splitbee.io/v1/table/${NOTION_BLOG_ID}`)
          .then((res) => {
            if(res.ok) {
              return res.json()
            } 
            throw res
          })
          .then(post => {
            setPosts(post)
          })
          .catch(error => {
            console.error("error tetching data: ", error)
    
          })
      }, [])
      
    return(
        <div>
            <h1>my awesome blog</h1>
            {Posts.map((post, index) => 
          <div key={index} > 
            <Link to={`/blog/${post.id}`}>
              {post.Name}
            </Link>
          </div>)}
        </div>
    )
}

export default Blog